import React from 'react';
import { Script } from "gatsby"

const FooterScript = () => {
  return (
    <>
      {/* <!-- Google tag (gtag.js) --> */}
      <Script async src="https://www.googletagmanager.com/gtag/js?id=G-V0R9C5E0S7" />
      <Script>
        {`
window.dataLayer = window.dataLayer || [];
function gtag(){dataLayer.push(arguments);}
gtag('js', new Date());
gtag('config', 'G-V0R9C5E0S7');
        `}
      </Script>
    </>
  )
};

export default FooterScript;
