import React, { FunctionComponent, useEffect, useState } from 'react';
import CircularProgress from '@mui/material/CircularProgress';
import './index.scss';
import { Container, AppBar, Button, Card, CardActions, CardContent, Grid, IconButton, Paper, TextField, Toolbar, Typography } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';

interface Props {
    articles: Article[];
    isShownProgressCircle: boolean;
}

interface Article {
    id: number;
    title: string|null;
    contents: string|null;
    date_time: string|null;
    modify_time: string|null;
    writer: string|null;
}

const ArticlesPage: FunctionComponent<Props> = ({
    articles, isShownProgressCircle,
}) => {
    const CONTENTS_MAX_LENGTH = 30;

    return (
        <div className="articles-page-comp">
            <AppBar position="static">
            <Toolbar>
                <IconButton
                    size="large"
                    edge="start"
                    color="inherit"
                    aria-label="menu"
                    sx={{ mr: 2 }}
                >
                    <MenuIcon />
                </IconButton>
                <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
                    News
                </Typography>
                </Toolbar>
            </AppBar>
            <Container maxWidth="lg" sx={{ paddingTop: '80px' }}>
                <Typography variant="h2" component="h1" sx={{ marginBottom: '40px' }}>뉴스 기사</Typography>
                {
                    isShownProgressCircle ?
                    <div style={
                        { height: '300px', display: 'flex', justifyContent: 'center', alignItems: 'center' }
                    }>
                        <CircularProgress />
                    </div>
                    :
                    <Grid container columns={{ xs: 2, lg: 4 }} spacing={{ xs: 4, lg: 2 }} >
                        {
                            articles.map(article => (
                                <Grid item key={article.id} xs={1} lg={1}>
                                <Card elevation={2}>
                                    <CardContent>
                                        <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                                            Word of the Day
                                        </Typography>
                                        <Typography variant="h5" component="div">
                                            {article.title}
                                        </Typography>
                                        <Typography sx={{ mb: 1.5 }} color="text.secondary">
                                            {article.date_time}
                                        </Typography>
                                        <Typography variant="body2">
                                            {article.contents ?
                                                article.contents.length > CONTENTS_MAX_LENGTH ?
                                                    article.contents.slice(0, CONTENTS_MAX_LENGTH)
                                                    :
                                                    article.contents
                                                :
                                                "내용이 비어있습니다."
                                            }
                                            {article.contents?.slice(0, 50)}
                                        </Typography>
                                    </CardContent>
                                    <CardActions>
                                        <Button size="small">See More</Button>
                                    </CardActions>
                                </Card>
                                </Grid>
                            ))
                        }
                    </Grid>
                }
            </Container>
        </div>
    );
};

export default ArticlesPage;
