import { PageProps } from "gatsby";
import React, { FunctionComponent, useEffect, useState } from "react";
import Head from "../lib/Head";
import tempArticlesData from '../lib/temp-articles-data';
import { Script } from "gatsby"
import ArticlesPage from '../view/component/page/ArticlesPage';
import FooterScript from "../lib/FooterScript";

interface Article {
  id: number;
  title: string|null;
  contents: string|null;
  date_time: string|null;
  modify_time: string|null;
  writer: string|null;
}

const Articles: FunctionComponent<PageProps> = ({ pageContext }) => {
  const [articles, setArticles] = useState<Article[]>([]);
  const [isShownProgressCircle, setIsShownProgressCircle] = useState<boolean>(false);

  const fetchArticles = () => {
      setIsShownProgressCircle(true);
      try {
          const url = 'http://jungwoohan-alb-api-server-1487794525.ap-northeast-2.elb.amazonaws.com/articles';
          fetch(url)
              .then(res => res.json())
              .then(json => setArticles(json))
              .then(_ => setIsShownProgressCircle(false));    
      } catch(e) {
          console.error(e);
          alert("Temp Data")
          setArticles(tempArticlesData);
          setIsShownProgressCircle(false);
      }
  }

  useEffect(() => {
      fetchArticles();
  }, []);
    
  return (
    <>
    <Head
      title="LGCNS Articles"
      description="LGCNS Articles"
      link={[
        {
          rel: 'stylesheet',
          href: "https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap"          
        }
      ]}
      additionalMeta={[
        {
          httpEquiv: 'Content-Security-Policy',
          content: 'upgrade-insecure-requests'
        }
      ]}
    />
    <ArticlesPage
      articles={articles}
      isShownProgressCircle={isShownProgressCircle}
    />
    <FooterScript />
    </>
  );
};

export default Articles;
